<template>
  <div class="scrollBar">
    <!-- 头部 -->
    <div class="header-nav" :class="{hasShare: $config.source_style >= 6}">
      <div class="nav-list" :class="{active: navList.length < 3}">
        <div class="nav-item" @click="setVideoType(index)" :class="{active: activeIndex === index}" v-for="(item, index) in navList" :key="index">{{item.name}}</div>
      </div>
      <div class="shareIcon" @click="setShare" v-if="$config.source_style >= 6"></div>
    </div>
    <!-- 搜索栏 -->
    <div class="search-bar">
      <div class="search-content" @click="toSearch">
        <div class="search-icon iconSize">
          <img src="../../assets/img/movie/white_search.png" alt="搜索">
        </div>
        <div class="serarch-text">搜索-史上最大最全的影视片库</div>
      </div>
    </div>
    <div id="movie" ref="movie">
      <div class="scrollTouch movieContent" ref="swipeLevel" v-if="showTabList">
          <!-- 推荐 -->
        <transition :name="animationName">
          <keep-alive>
            <recommend-list v-if="activeIndex === 0 && showTabList" :movieList="movieList" @childSearch="childSearch" @swipe="swipe" @watchMore="watchMore" :activeIndex="0"></recommend-list>
          </keep-alive>
        </transition>
        <template v-for="(item, index) in navList">
          <transition :name="animationName" :key="index">
            <!-- 电影 -->
            <keep-alive>
              <tele-play v-if='activeIndex === index && index > 0' :refIndex="activeIndex" @swipe="swipe" @childSearch="childSearch" @isShowFilterFn="isShowFilterFn" :activeIndex="item.id"></tele-play>
            </keep-alive>
          </transition>
          <!-- <transition :name="animationName">
            <keep-alive>
              <tele-play v-if='activeIndex === 2'  @swipe="swipe" @childSearch="childSearch" @isShowFilterFn="isShowFilterFn" :activeIndex="2"></tele-play>
            </keep-alive>
          </transition>
          <transition :name="animationName">
            <keep-alive>
              <tele-play v-if='activeIndex === 3' @swipe="swipe" @childSearch="childSearch" @isShowFilterFn="isShowFilterFn" :activeIndex="3"></tele-play>
            </keep-alive>
          </transition>
          <transition :name="animationName">
            <keep-alive>
              <tele-play v-if='activeIndex === 4' @swipe="swipe" @childSearch="childSearch" @isShowFilterFn="isShowFilterFn" :activeIndex="4"></tele-play>
            </keep-alive>
          </transition> -->
        </template>
        <!-- 电视剧分类 -->
      </div>
    </div>
    <download></download>
    <ShareCommon @closeShare='closeShare' @getArc='getArc' :fatherParams='fatherParams' v-if="showConfirm"></ShareCommon>
    <carShare @shareSuccess='shareSuccess' v-if="fatherParams" :appShare='appShare' :fatherParams='fatherParams'></carShare>
    <shareBox v-if="showShare" @shareAction='shareAction'></shareBox>
    <search v-if="movie.showSearch" :kw="kw" :activeType="movieType" @closeFn="closeFn"></search>
  </div>
</template>
<script>
import {mapState, mapMutations} from 'vuex'
import ShareCommon from '@/components/share/shareCommon'
import TelePlay from '@//components/movie/teleplay/index'
import carShare from '@/components/share/carShare'
import shareBox from '@/components/common/share_box'
import RecommendList from '@/components/movie/recommend'
import Search from '@/components/movie/common/showSearch'
import download from '@/components/common/download'
export default {
  components: {
    ShareCommon,
    TelePlay,
    carShare,
    shareBox,
    RecommendList,
    Search,
    download
  },
  computed: {
    ...mapState({
      commonState: state => state.common,
      movie: state => state.movie
    })
  },
  data () {
    return {
      isShowFilter: false,
      movieList: [],
      showTabList: false,
      tabList: [],
      kw: '',
      movieType: 0,
      appShare: {
        count: 0
      },
      animationName: '',
      showShare: false,
      showConfirm: false,
      fatherParams: null,                   // 分享参数
      activeIndex: 0,                     // 电影激活分类
      filterActive: false,                    // 筛选激活分类
      navIndex: 0,                        // 推荐分类激活
      navScrollTop: {},
      navId: {
        推荐: 0,
        电影: 1,
        电视剧: 2,
        综艺: 3,
        动漫: 4
      },
      navList: [],
      navList1: [{
        name: '推荐',
        id: 0
      },
      {
        name: '电影',
        id: 1
      },
      {
        name: '电视剧',
        id: 2
      },
      {
        name: '综艺',
        id: 3
      },
      {
        name: '动漫',
        id: 4
      }]                              // 电影分类
    }
  },
  watch: {
    activeIndex (val) {
      this.movie_setIndex(this.navList[this.activeIndex].id)
    }
  },
  methods: {
    ...mapMutations([
      'common_login_box',
      'movie_back',
      'movie_active',
      'movie_setShowSearch',
      'movie_setIndex'
    ]),
    closeFn () {
      this.movie_setShowSearch(false)
    },
    childSearch ({item, movieType}) {
      if (movieType === 2 && item.ext_class && item.ext_class.indexOf('动画') > -1) {
        movieType = 4
      }
      this.kw = item.title
      this.movieType = movieType
      this.movie_setShowSearch(true)
    },
    // 跳转至搜索
    toSearch () {
      this.$router.push('/movie/search')
    },
    // 返回
    back () {

    },
    // 更多
    watchMore (index) {
      this.navScrollTop[this.activeIndex] = this.$refs.movie.scrollTop
      this.activeIndex = index
      if (this.navScrollTop[this.activeIndex]) {
        this.$nextTick(() => {
          this.$refs.movie.scrollTop = this.navScrollTop[this.activeIndex]
        })
      } else {
        this.$refs.movie.scrollTop = 0
      }
    },
    // 分享完成
    shareSuccess () {
      this.showShare = false
      this.showConfirm = false
    },
    getArc (e) {

    },
    // 分享
    shareAction (actions) {
      if (actions === 'cancel') {
        this.showShare = false
      } else if (actions === 'system') {
        let msg = {
          title: this.fatherParams.title,
          content: this.fatherParams.desc,
          thumbs: [this.fatherParams.img_url],
          href: this.fatherParams.shareLink
        }
        this.$utils.plusReady(plus => {
          plus.share.sendWithSystem(msg, res => {
            this.share_box = false
          }, err => {
            this.share_box = false
            console.log(err)
          })
        })
      } else if (actions === 'msg') {
        let msg = window.plus.messaging.createMessage(window.plus.messaging.TYPE_SMS)
        msg.to = ['10086']
        msg.bodyType = 'text'
        msg.body = `${this.fatherParams.title}↓↓↓点击查看↓↓↓${this.$config.currentWebUrl}?path=${this.$route.path}&inviteCode=${this.$store.state.common.userInfo.invitation}`
        window.plus.messaging.sendMessage(msg)
      } else {
        let index = 0
        switch (actions) {
          case 'wechat': index = 1
            break
          case 'friend': index = 2
            break
          case 'qq': index = 3
            break
          case 'sinaweibo': index = 4
            break
          default: console.log(actions)
        }
        this.currentShare(index)
      }
    },
    // app 分享
    currentShare (index) {
      if (this.$config.source_style >= 6) {
        // index 1 微信好友 2 微信朋友圈 3 为qq 4新浪微博
        let msg = {
          title: this.fatherParams.title,
          content: this.fatherParams.desc,
          thumbs: [this.fatherParams.img_url],
          href: this.fatherParams.shareLink
        }
        let id = 'weixin'
        if (index === 1) {
          id = 'weixin'
          msg.type = 'web'
          msg.extra = {
            scene: 'WXSceneSession'
          }
        } else if (index === 2) {
          id = 'weixin'
          msg.type = 'web'
          msg.extra = {
            scene: 'WXSceneTimeline'
          }
        } else if (index === 3) {
          id = 'qq'
          // msg.type = 'text'
        } else if (index === 4) {
          id = 'sinaweibo'
        }
        setTimeout(() => {
          // w.close()
        }, 5000)
        if (index === 4 && !this.commonState.shares['sinaweibo'].nativeClient) {
          this.$toast({
            message: '请先安装新浪微博',
            duration: 1500
          })
        } else if (index === 4 && this.commonState.shares['sinaweibo'].nativeClient && !this.commonState.shares['sinaweibo'].authenticated) {
          this.commonState.shares['sinaweibo'].authorize(() => {
            this.commonState.shares['sinaweibo'].send(msg, function (res) {
              this.$toast({
                message: '分享成功',
                duration: 1500
              })
            }, err => {
              // w.close()
              this.showShare = false
              if (err.code === -2) {
                this.$toast({
                  message: '已取消分享',
                  duration: 1500
                })
                this.shareSuccess({
                  type: 'app',
                  way: '3',
                  success: 2
                })
              } else {
                this.$toast({
                  message: '分享失败',
                  duration: 1500
                })
              }
            })
          })
        } else {
          this.commonState.shares[id].send(msg, res => {
            if (index === 3) {
              // w.close()
              this.$toast({
                message: '分享成功',
                duration: 1500
              })
            }
            this.shareSuccess({type: 'app', way: index})
            this.showShare = false
          }, err => {
            // w.close()
            this.showShare = false
            if (err.code === -2) {
              this.$toast({
                message: '已取消分享',
                duration: 1500
              })
              this.shareSuccess({
                type: 'app',
                way: '3',
                success: 2
              })
            } else {
              this.$toast({
                message: '分享失败',
                duration: 1500
              })
            }
          })
        }
      }
    },
    // 设置电影类型
    setVideoType (index) {
      if (this.activeIndex === 0) {
        this.navScrollTop[this.activeIndex] = document.getElementById('movieRecommend').scrollTop
      } else {
        this.navScrollTop[this.activeIndex] = document.getElementsByClassName('movieInner' + this.activeIndex)[0] && document.getElementsByClassName('movieInner' + this.activeIndex)[0].scrollTop
      }
      this.activeIndex = index
      if (this.navScrollTop[this.activeIndex]) {
        this.$refs.movie.scrollTop = this.navScrollTop[this.activeIndex]
        this.$nextTick(() => {
          if (this.activeIndex === 0) {
            console.log('回到首页')
            this.$refs.movie.scrollTop = this.navScrollTop[this.activeIndex]
          } else {
            console.log('当前movie')
            if (document.getElementsByClassName('movie')[0]) {
              document.getElementsByClassName('movieInner' + this.activeIndex)[0].scrollTop = this.navScrollTop[this.activeIndex]
            }
          }
        })
      } else {
        this.$refs.movie.scrollTop = 0
      }
    },
    // 去下载电影
    toDownload () {
      this.$utils.openDownUrl()
    },
    // 设置激活分类
    setFilterActive (index) {
      this.filterActive = index
    },
    isShowFilterFn (flag) {
      this.isShowFilter = flag
    },
    // 设置导航
    setNavIndex (index) {
      this.navScrollTop[this.activeIndex] = this.$refs.movie.scrollTop
      this.navIndex = index
      if (this.navScrollTop[this.activeIndex]) {
        this.$nextTick(() => {
          this.$refs.movie.scrollTop = this.navScrollTop[this.activeIndex]
        })
      }
    },
    // 监听移动状态听状态
    handleTopChange (status) {
      this.moveTranslate = 1
      this.topStatus = status
    },
    // 监听移动距离
    translateChange (translate) {
      const translateNum = +translate
      this.translate = translateNum.toFixed(2)
      this.moveTranslate = (1 + translateNum / 70).toFixed(2)
    },
    
    // 设置分享
    setShare () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
      if (this.$config.source_style >= 6) {
        this.showShare = true
      } else if (this.$utils.isWx()) {
        this.showConfirm = true
      }
    },
    closeShare () {
      this.showConfirm = false
    },
    // 生成分享文案
    getShareParams () {
      this.fatherParams = {
        title: '海量高清视频在线观看，永久免费无广告，全网最新最全  - 掘金宝影视资讯',
        desc: '掘金宝拥有全球最大最新高清影视资源，无需下载，永久免费无广告~',
        img_url: 'https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/share_logo.png'
      }
      let linkParams = {}
      if (this.appShare.path) {
        linkParams.path = this.appShare.path
      } else {
        linkParams = {
          download_path: '/download/index.html',
          invite: this.$store.state.common.userInfo.invitation
        }
      }
      if (this.commonState.login_bol) {
        linkParams.inviteCode = this.commonState.userInfo.invitation
      }
      this.$api.get('USE_DOMAIN', { type: 'temp' }, res => {
        this.$config.currentWebUrl = res.data.domain
      })
      if (this.$config.source_style >= 6) {
        this.fatherParams.shareLink = `${this.$config.currentWebUrl}/?${this.$qs.stringify(linkParams)}`
      } else {
        this.fatherParams.shareLink = `${location.origin}/?${this.$qs.stringify(linkParams)}`
      }
    },
    // 左滑动
    swipeLeft () {
      if (this.activeIndex < this.navList.length - 1) {
        this.setVideoType(this.activeIndex + 1)
      }
    },
    // 右滑动
    swipeRight () {
      // this.animationName = 'RightList'
      if (this.activeIndex > 0) {
        this.setVideoType(this.activeIndex - 1)
      }
    },
    swipe ({actions}) {
      if (actions === 'swipeleft') {
        this.swipeLeft()
      } else if (actions === 'swiperight') {
        this.swipeRight()
      }
    },
    getTab () {
      this.$api.get('TAB_LIST', {}, res => {
        let arr = []
        arr = res.data.nav.map(item => {
          let obj = {}
          obj.name = item.name
          obj.id = this.navId[item.name]
          return obj
        })
        this.navList = [{name: '推荐', id: 0}, ...arr]
        this.movieList = res.data.tab
        this.showTabList = true
      })
    }
  },
  beforeRouteLeave (to, from, next) {
    if (this.$refs.movie) {
      this.$utils.cache('movieScroll', this.$refs.movie.scrollTop)
    }
    this.movie_back(to)
    if (to.name !== 'FilmDetail') {
      if (this.movie.showSearch) {
        this.movie_setShowSearch(false)
        next(false)
      } else {
        next()
      }
    } else {
      next()
    }
  },
  created () {
    this.getTab()
  },
  mounted () {
    
  },
  activated () {
    this.$store.commit('common_set_imgIndex', 2)
    this.movie_active()
    this.getShareParams()
    if (this.$utils.cache('movieScroll')) {
      this.$nextTick(() => {
        this.$refs.movie.scrollTop = this.$utils.cache('movieScroll')
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.scrollBar {
  height: 100vh;
  overflow: hidden;
  &.Router {
    transition: all 0s ease!important;
  }
  .serarch-text {
    color: rgba($color: #fff, $alpha: .8)
  }
  .movieContent {
    height: calc(100vh - 188px);
    padding-bottom: 100px;
    box-sizing: border-box;
    overflow: hidden;
  }
  // 搜索
  .search-bar {
    padding: 20px 30px 20px;
    position: absolute;
    left: 0;
    background: #ffaa00;
    z-index: 2;
    right: 0;
    top: 88px;
    .search-content {
      height: 66px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 26px;
      line-height: 38px;
      background: rgba($color: #DD980E, $alpha: .8);
      color: #fff;
      border-radius: 33px;
      .search-icon {
        width: 40px;
        height: 40px;
        margin-right: 30px;
      }
    }
  }
  .header-nav {
    display: flex;
    flex-flow: row nowrap;
    height: 88px;
    box-shadow: 0 1px 0px 0 #f5f5f5;
    position: absolute;
    left: 0;
    right: 0;
    z-index: 2;
    top: 0;
    background: #FFAA00 ;
    &.hasShare {
      padding-right: 100px;
    }
    .nav-list {
      flex: 1;
      min-width: 0;
      display: flex;
      padding: 0 30px;
      box-sizing: border-box;
      justify-content: space-between;
      flex-flow: row nowrap;
      align-items: center;
      color: #fff;
      overflow: auto;
      &.active {
        justify-content: space-around;
      }
      .nav-item {
        font-size: 32px;
        line-height: 45px;
        &.active{
          font-size: 40px;
          font-weight: 900;
          line-height: 56px;
        }
      }
    }
    .shareIcon {
      position: absolute;
      background-image: url('../../assets/img/movie/share-icon.png');
      height: 88px;
      z-index: 1;
    }
  }
}
#movie {
  box-sizing: border-box;
  overflow-y: auto;
  overflow-x: hidden;
  height: 100vh;
  padding-top: 186px;
  .mint-loadmore-top {
    margin-bottom: 10px;
  }
}
</style>



