<template>
<div class="scrollBox">
   <!-- 筛选固定位置 -->
  <div class="filter flex-row fixedTop" :class="{noBorder: activeIndex > 2}" style="z-index: 4">
    <div class="filter-item" :class="{active: filterActive === 0}" @click="setFilterActive(0)">
      <div class="filter-icon">
        <img v-if="filterActive === 0" src="../../../assets/img/movie/recomd_active.png" alt="">
        <img v-else src="../../../assets/img/movie/recomd.png" alt="">
      </div>
      <div class="text">推荐</div>
    </div>
    <div class="filter-item" :class="{active: filterActive === 1}" @click="setFilterActive(1)">
      <div class="filter-icon">
        <img v-if="filterActive === 1" src="../../../assets/img/movie/filter_active.png" alt="">
        <img v-else src="../../../assets/img/movie/filter.png" alt="">
      </div>
      <div class="text">筛选</div>
    </div>
  </div>
  <div class="movie" ref="wrapper" :class="[`active${refIndex} movieInner${refIndex}`, {isBrowser: $config.source_style < 6}]">
    <!-- 推荐 -->
      <transition name='RightList'>
        <keep-alive>
          <div class="recomdList" v-if="filterActive === 0">
            <div class="recomd-nav-content" :class="{isIos: $utils.isIos()}">
              <div class="recomd-nav" :class="`recomdNav${recomdNav.length}`" v-if="activeIndex <= 2">
                <div class="recomd-nav-item" :class="{active: index === navIndex}" v-for="(item, index) in recomdNav" :key="index" @click="setNavIndex(index)">{{item}}</div>
              </div>
            </div>
            <mt-loadmore :top-method="loadTop" @translate-change="translateChange" @top-status-change="handleTopChange" ref="loadmore">
              <div slot="top" class="mint-loadmore-top" :class="{loading: topStatus==='loading'}">
                <!-- 向下 -->
                <div class="rotateLoading" v-show="topStatus !== 'loading'">
                  <div class="download-icon download-gray">
                    <img src="../../../assets/img/movie/download_gray.png" alt="加载">
                  </div>
                  <div class="text">{{topStatus === 'pull' ? '下拉推荐' : '松开刷新'}}</div>
                </div>
                <!-- 加载中 -->
                <div class="rotateLoading" v-show="topStatus === 'loading'">
                  <div class="download-icon">
                    <img src="../../../assets/img/movie/download.gif" alt="加载">
                  </div>
                  <div class="text">正在努力加载</div>
                </div>
              </div>
              <!-- 推荐滚动加载 -->
              <div class="scrollTouch" infinite-scroll-distance="50"  v-infinite-scroll="loadMore">
                <List :list='recommendList' ref="recommendList" @setTip='setTip' @toSearch="childSearch" :filterType='filterType' :movieType='movieType' :activeIndex="activeIndex" :type='listType'></List>
                <!-- <video-loading v-if="recommendList.length === 0 && page === 1 && !isRefresh"></video-loading> -->
                <video-loading v-if="recommendList.length === 0 && page === 1"></video-loading>
                <div class="complete" v-if="loading === 2">没有更多数据了！</div>
                <loading :page="page" :loading="loading"></loading>
              </div>
            </mt-loadmore>
          </div>
        </keep-alive>
      </transition>
      <!-- 筛选 -->
      <transition name="leftList">
        <keep-alive>
          <div class="filterList" v-if="filterActive === 1">
            <keep-alive>
              <filter-list :type="activeIndex" @getFilter="getFilter"></filter-list>
            </keep-alive>
            <mt-loadmore :top-method="loadTop" @translate-change="translateChange" @top-status-change="handleTopChange" ref="loadmore">
              <div slot="top" class="mint-loadmore-top" :class="{loading: topStatus==='loading'}">
                <!-- 向下 -->
                <div class="rotateLoading" v-show="topStatus !== 'loading'">
                  <div class="download-icon download-gray">
                    <img src="../../../assets/img/movie/download_gray.png" alt="加载">
                  </div>
                  <div class="text">{{topStatus === 'pull' ? '下拉推荐' : '松开刷新'}}</div>
                </div>
                <!-- 加载中 -->
                <div class="rotateLoading" v-show="topStatus === 'loading'">
                  <div class="download-icon">
                    <img src="../../../assets/img/movie/download.gif" alt="加载">
                  </div>
                  <div class="text">正在努力加载</div>
                </div>
              </div>
              <div class="scrollTouch"  infinite-scroll-distance="50"  v-infinite-scroll="loadMoreFilter">
                <List :class="{'list-top': true}" @toSearch='childSearch' ref="filterList" :activeIndex="activeIndex" :list='list.slice(0, 99)' :type="filterListType"></List>
                <video-loading v-if="list.length === 0 && filterLoading === 1"></video-loading>
                <!-- <video-loading v-if="list.length === 0 && filterLoading === 1 && !isRefresh"></video-loading> -->
              </div>
              <loading :loading="filterLoading" :page="filterPage"></loading>
              <div v-if="filterLoading === 2">
                <!-- 加载完无数据 -->
                <div class="search" v-if="list.length === 0">
                  <div class="text">没有找到想看的影视</div>
                  <div class="saarch-content flex-row">
                    <div class="text" @click="toSearch">立即搜索</div>
                    <div class="arrowEl"></div>
                  </div>
                  <!-- <div class="button" @click="toSearch">立即搜索</div> -->
                </div>
                <!-- 大于0条小于99条 -->
                <div class="search" v-else-if="list.length < 99">
                  <div class="text">没有找到想看的影视</div>
                  <div class="saarch-content flex-row">
                    <div class="text" @click="toSearch">立即搜索</div>
                    <div class="arrowEl"></div>
                  </div>
                  <!-- <div class="button" @click="toSearch">立即搜索</div> -->
                </div>
                <div class="search" v-else>
                  <div class="text">只展示99个相关资源，搜索可查看全部</div>
                  <div class="button" @click="toSearch">立即搜索</div>
                </div>
              </div>
            </mt-loadmore>
          </div>
        </keep-alive>
      </transition>
        <!--  -->
    <prompt-box
      :dropBol="showTip"
      @leftBtn="showTip=false"
      @rightBtn="confirmBtn"
      leftTxt="取消"
      rightTxt="确认"
      :contentTxt="confirmText"
    >
    </prompt-box>
  </div>
</div>
 
</template>
<script>
import {mapState, mapMutations} from 'vuex'
import IndexFooter from '@/components/common/index-footer'
import ShareCommon from '@/components/share/shareCommon'
import List from '@/components/movie/common/list'
import FilterList from '@/components/movie/common/filter'
import swiper from '../swiper'
import PromptBox from '../../common/prompt-box'
import VideoLoading from '@/components/movie/common/video-loading'
import Loading from '@/components/movie/common/list-loading'
export default {
  components: {
    ShareCommon,
    IndexFooter,
    swiper,
    List,
    FilterList,
    PromptBox,
    VideoLoading,
    Loading
  },
  computed: {
    ...mapState({
      commonState: state => state.common,
      movie: state => state.movie
    })
  },
  props: {
    activeIndex: {
      type: Number
    },
    refIndex: {
      type: Number
    }
  },
  data () {
    return {
      isRefresh: false,                 // 正在刷新
      movieType: 0,
      filterListType: 1,                // 筛选列表类型
      showSearch: false,
      kw: '',
      recommendList: [],
      page: 1,
      filterPage: 1,
      filterLoading: 0,
      loading: 0,
      confirmText: '',
      topStatus: '',
      showTip: false,
      tipIndex: '',
      isShowFilter: false,
      wrapperHeight: '',
      filterActive: 0,                    // 筛选激活分类
      navIndex: 0,                        // 推荐分类激活
      navList: [{
        name: '推荐',
        id: 0
      },
      {
        name: '电影',
        id: 1
      },
      {
        name: '电视剧',
        id: 2
      },
      {
        name: '综艺',
        id: 3
      },
      {
        name: '动漫',
        id: 4
      }],                                 // 电影分类
      recomdNavObj: {
        1: ['影院热映', '近期热门', '即将上映'],
        2: ['国产剧', '韩剧', '日剧', '美剧', '港剧'],
        3: [],
        4: []
      },
      listType: 0,
      filterType: '',
      swiperList: [],                                  // 轮播分类
      recomdNav: [],                                  // 筛选推荐
      list: [],
      type_name: '',
      year: '',
      area: ''
    }
  },
  methods: {
    ...mapMutations([
      'common_login_box',
      'movie_setShowSearch'
    ]),
    // 下拉刷新状态
    loadTop () {
      this.isRefresh = true
      if (this.filterActive === 0) {
        this.recommendList = []
        this.loading = 1
        this.page = 1
        this.getRecomdList(res => {
          this.isRefresh = false
          this.recommendList = res.data.data
          setTimeout(() => {
            this.$refs.loadmore.onTopLoaded()
          }, 400)
        })
      } else if (this.filterActive === 1) {
        this.list = []
        this.filterPage = 1
        this.filterLoading = 1
        this.getFilterList(res => {
          this.list = res.data.data
          this.isRefresh = false
          setTimeout(() => {
            this.$refs.loadmore.onTopLoaded()
          }, 400)
        })
      }
      setTimeout(() => {
        this.$refs.loadmore.onTopLoaded()
      }, 5000)
    },
    // 筛选
    loadMoreFilter () {
      if (this.filterLoading > 0) {
        return
      }
      this.filterLoading = 1
      this.getFilterList(res => {
        this.list = [...this.list, ...res.data.data]
      })
    },
    // 推荐下拉加载
    loadMore () {
      if (this.loading > 0) {
        return
      }
      this.loading = 1
      this.getRecomdList(res => {
        this.recommendList = [...this.recommendList, ...res.data.data]
      })
    },
    // 监听移动距离
    translateChange (translate) {
      const translateNum = +translate
      this.translate = translateNum.toFixed(2)
      this.moveTranslate = (1 + translateNum / 70).toFixed(2)
    },
    // 监听移动状态听状态
    handleTopChange (status) {
      this.moveTranslate = 1
      this.topStatus = status
    },
    // 返回
    back () {

    },
    // 订阅与取消订阅
    confirmBtn () {
      let index = this.tipIndex
      let item = this.recommendList[index]
      let data = {
        vod_id: item.id,
        type: 'douban'
      }
      let url = ''
      if (item.is_subscription === 0) {
        data.status = 1
        url = 'MOVIE_SUBSCRIBE'
      } else if (item.is_subscription === 1) {
        url = 'MOVIE_SUBSCRIBE_CANCEL'
        data.id = item.subscription_id
        data.status = 2
      }
      this.$api.post(url, data, res => {
        this.showTip = false
        let obj = this.recommendList[index]
        obj.is_subscription = obj.is_subscription === 1 ? 0 : 1
        // 订阅成功变为1
        if (obj.is_subscription === 1) {
          obj.watch_num = obj.watch_num ? obj.watch_num++ : 1
          obj.subscription_id = res.data.id
          this.$toast({
            message: '已开启提醒',
            duration: 1500
          })
        } else if (obj.is_subscription === 0) {
          obj.watch_num = obj.watch_num--
          this.$toast({
            message: '已关闭提醒',
            duration: 1500
          })
        }
        this.$set(this.recommendList, index, obj)
      }, res => {

      })
    },
    // 跳转至搜索
    toSearch () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
      this.$router.push('/movie/search')
    },
    toAward () {
      this.$router.push({
        path: '/AwardFeedback',
        query: {
          from: 'movie'
        }
      })
    },
    closeFn () {
      this.movie_setShowSearch(false)
    },
    childSearch ({item, movieType}) {
      this.$emit('childSearch', {item, movieType})
    },
    // 设置电影类型
    setVideoType (index) {
      this.activeIndex = index
    },
    // 去下载电影
    toDownload () {
      this.$utils.openDownUrl()
    },
    // 跳转至解析地址
    toVip () {
      this.$router.push('/movie_decode')
    },
    // 设置激活分类
    setFilterActive (index) {
      this.filterActive = index
      this.movieEl.scrollTop = 0
      this.initSwipe()
      if (index === 0) {

      } else if (index === 1) {
        this.filterLoading = 1
        if (this.list.length === 0) {
          this.filterPage = 1
          this.getFilterList(res => {
            this.list = [...this.list, ...res.data.data]
          })
        }
      }
    },
    // 设置和取消上映提醒
    setTip ({index}) {
      this.tipIndex = index
      if (this.recommendList[index].is_subscription === 1) {
        this.confirmText = '你将无法接受最新影片提醒，是否不再提醒？'
      } else {
        this.confirmText = '影片上映时，将提醒你免费观看，是否提醒？'
      }
      this.showTip = true
    },
    // 获取推荐列表
    getRecomdList (cb) {
      let data = {
        page: this.page,
        type: this.filterType
      }
      if (this.movie.movieIndex !== this.activeIndex || this.filterActive === 1) {
        this.loading = 0
        return
      }
      if (this.category) {
        data.category = this.category
        if (data.category === '即将上映') {
          this.movieType = 1
          this.listType = 6
        } else if (data.category === '近期热门') {
          this.listType = 7
          this.movieType = 1
        } else if (data.category === '影院热映') {
          this.listType = 7
        } else {
          this.movieType = 2
        }
      }
      this.$api.get('MOVIE_RECOMMEND', data, res => {
        this.initSwipe()
        cb && cb(res)
        this.loading = 0
        if (this.page > 9 || this.page >= res.data.last_page) {
          this.loading = 2
        }
        this.page++
      })
    },
    // 设置导航
    setNavIndex (index) {
      this.navIndex = index
      this.page = 1
      this.recommendList = []
      this.category = this.recomdNav[this.navIndex]
      this.loading = 1
      this.getRecomdList(res => {
        this.recommendList = res.data.data
        // this.recomdNav = res.data.category
      })
    },
    // 获取列表
    getFilter (e) {
      if (e) {
        this.type_name = e.type ? e.type : ''
        this.area = e.area ? e.area : ''
        this.year = e.year ? e.year : ''
        this.filterPage = 1
        this.list = []
        this.filterLoading = 1
        this.getFilterList(res => {
          this.list = res.data.data
        })
      }
    },
    getFilterList (cb) {
      let data = {
        page: this.filterPage,
        per_page: 12,
        type: this.filterType
      }
      if (this.type_name && this.type_name !== '全部') {
        data.type_name = this.type_name
      }
      if (this.year && this.year !== '全部') {
        data.year = this.year
      }
      if (this.area && this.area !== '全部') {
        data.area = this.area
      }
      if (this.movie.movieIndex !== this.activeIndex || this.filterActive !== 1) {
        this.filterLoading = 0
        return
      }
      this.$api.get('MOVIE_SEARCH', data, res => {
        this.initSwipe()
        this.filterLoading = 0
        if (this.filterPage > 9 || this.filterPage >= res.data.last_page) {
          this.filterLoading = 2
        }
        this.filterPage++
        cb && cb(res)
      })
    },
    // 滚动事件
    scrollFn () {
      if (this.$refs.filterMore) {
        if (this.el.scrollTop > (this.$utils.getElposition(this.$refs.filterMore) - this.$refs.filterMore.clientHeight * 2)) {
          this.isShowFilter = true
        } else {
          this.isShowFilter = false
        }
      }
    },
    // 获取轮播数据
    getSwiperList () {
      let data = {
        type: this.filterType
      }
      this.$api.get('MOVIE_SWIPER', data, res => {
        this.swiperList = res.data
      })
    },
    // 初始化数据
    initData () {
      this.getSwiperList()
    },
    leftList () {
      // if (this.filterActive === 0) {
      //   this.setFilterActive(1)
      // } else if (this.filterActive === 1) {
        
      // }
      this.$emit('swipe', {
        actions: 'swipeleft'
      })
    },
    rightList () {
      // if (this.filterActive === 1) {
      //   this.setFilterActive(0)
      // } else if (this.filterActive === 0) {
      //   this.$emit('swipe', {
      //     actions: 'swiperight'
      //   })
      // }
      this.$emit('swipe', {
        actions: 'swiperight'
      })
    },
    initSwipe () {
      this.$nextTick(() => {
        if (this.$refs.filterList) {
          this.$refs.filterList.$el.removeEventListener('swipeleft', this.leftList)
          this.$refs.filterList.$el.removeEventListener('swiperight', this.rightList)
          this.$refs.filterList.$el.addEventListener('swipeleft', this.leftList)
          this.$refs.filterList.$el.addEventListener('swiperight', this.rightList)
        }
        if (this.$refs.recommendList) {
          this.$refs.recommendList.$el.removeEventListener('swipeleft', this.leftList)
          this.$refs.recommendList.$el.removeEventListener('swiperight', this.rightList)
          this.$refs.recommendList.$el.addEventListener('swipeleft', this.leftList)
          this.$refs.recommendList.$el.addEventListener('swiperight', this.rightList)
        }
      })
    }
  },
  created () {
    this.filterType = this.navList[this.activeIndex].name
    this.tempIndex = this.activeIndex
    if (this.activeIndex === 1) {
      this.listType = 0
      this.movieType = 1
      this.filterListType = 10
    } else {
      this.listType = 0
      this.movieType = 0
      this.filterListType = 1
    }
  },
  mounted () {
    this.movieEl = document.getElementById('movie')
    this.movieEl.scrollTop = 0
    this.recomdNav = this.recomdNavObj[this.activeIndex]
    this.loading = 1
    this.page = 1
    this.category = this.recomdNav[0]
    if (this.activeIndex === 1) {
      this.listType = 7
    } else {
      this.listType = 0
      this.movieType = 2
    }
  },
  watch: {
    'isShowFilter' (flag) {
      this.$emit('isShowFilterFn', flag)
    },
    activeIndex (a) {

    }
  },
  activated () {
    this.$nextTick(() => {
      this.wrapperHeight = this.$refs.wrapper.clientHeight
    })
    if (this.recommendList.length === 0) {
      this.loading = 1
      this.page = 1
      this.getRecomdList((res) => {
        this.recommendList = res.data.data
      })
    }
    this.initSwipe()
    this.el = document.getElementById('movie')
    this.el.addEventListener('scroll', this.scrollFn)
  }
}
</script>

