        box-sizing: border-box;
        &.recomdNav3 {
          padding: 30px 30px 0;
          display: flex;
          justify-content: space-between;
        }
        &.recomdNav5 {
          display: flex;
          justify-content: space-between;
          padding: 30px;
          box-sizing: border-box;
          > .recomd-nav-item {
            margin: 0;
          }
        }
        .recomd-nav-item {
          display: inline-block;
          min-width: 130px;
          padding: 0 20px;
          display: inline-block;
          height: 54px;
          font-size: 30px;
          text-align: center;
          box-sizing: border-box;
          color: #666;
          padding-top: 14px;
          border: 1px solid #F4A82B;
          border-radius: 27px;
          margin-right: 15px;
          &.active {
            background: #F4A82B;
            color: #fff;
          }
          &:last-child {
            margin-right: 0
          }
        }
      }
    }
    
  }
  // 推荐列表
  .filterList {
    min-height: 900px;
    .search {
      padding: 65px 20px;
      .text {
        font-size: 26px;
        color: #666;
        text-align: center;
      }
      .saarch-content {
        .text {
          color: #4a90e2;
          padding: 30px 0px;
          line-height: 45px;
        }
        .arrowEl {
          width: 12px;
          height: 12px;
          border-color: #4a90e2;
        }
      }
      .button {
        border: 1px solid #C9A166;
        border-radius: 43px;
        margin: 0 auto;
        width: 370px;
        height: 70px;
        color: #C9A166;
        margin-top: 30px;
        line-height: 70px;
        text-align: center;
      }
    }
  }
  &.active1 {
    .recomdList {
      .recomd-nav {
        padding: 30px;
      }
      .recomd-nav-item {
        flex: 1;
        &:nth-child(2) {
          margin: 0 84px;
        }
      }
    }
  }
  .mint-loadmore-top {
    margin-top: -140px;
    height: 140px;
    padding-top: 30px;
    box-sizing: border-box;
    transition: all margin .4s;
    &.loading {
      margin-top: -100px;
    }
    .text{
      white-space: nowrap;
      text-align: center;
      font-size: 22px;
      color: #999;
    }
  }
  .loading-logo {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #666;
    font-size: 28px;
    margin: 0 auto 20px;
    color: #ffcc00;
    .logo {
      width: 40px;
      height: 40px;
      margin-right: 10px;
      animation: rotateFn 600ms linear infinite;
    }
    @keyframes rotateFn {
      form {
        transform: rotateZ(0deg);
      }
      to {
        transform: rotateZ(360deg);
      }
    }
  }
  .rotateLoading {
    height: 120px;
    .download-icon{
      width: 60px;
      height: 60px;
      overflow: hidden;
      margin: 0 auto;
      img {
        width: 100%;
      }
    }
    background-size: 100%;
    margin: 0 auto;
    // &.is-rotate {
    //   transform: rotate(180deg)
    // }
  }
}
</style>



