<template>
  <div class='filter-list'>
    <div class="filter-type" ref="filterType" v-for="(item, index) in filterData[type]" :key="index">
      <div class="filter-item-list" :class="{active: index1 === activeArr[index]}" @click="setFilter(index, index1)" v-for="(filter, index1) in item" :key="index1">{{filter}}</div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    type: {
      type: Number,
      default: 1              // 类型1为电影， 2为电视剧 3为综艺 4为动漫
    },
    filterType: {
      type: String
    }
  },
  data () {
    return {
      filterList: [],
      filterData: {                       // 1,电影 2电视剧 3 综艺 4动漫
        1: {
          type: ['全部', '动作片', '喜剧片', '爱情片', '科幻片', '恐怖片', '剧情片', '战争片', '纪录片', '音乐片', '微电影', '其他'],
          area: ['全部', '大陆', '香港', '台湾', '美国', '韩国', '日本', '泰国', '新加坡', '马来西亚', '印度', '英国', '法国', '加拿大', '西班牙', '俄罗斯', '其它'],
          year: ['全部', '2019', '2018', '2017', '2016', '2015', '2011-2014', '2000-2010', '90年代', '80年代', '更早']
        },
        2: {
          type: ['全部', '国产剧', '港台剧', '日韩剧', '欧美剧'],
          area: ['全部', '大陆', '香港', '台湾', '美国', '韩国', '日本', '泰国', '新加坡', '马来西亚', '印度', '英国', '法国', '加拿大', '西班牙', '俄罗斯', '其它'],
          year: ['全部', '2019', '2018', '2017', '2016', '2015', '2011-2014', '2000-2010', '90年代', '80年代', '更早']
        },
        3: {
          area: ['全部', '内地', '港台', '日韩', '欧美', '其他'],
          year: ['全部', '2019', '2018', '2017', '2016', '2015', '2011-2014', '2000-2010', '90年代', '80年代', '更早']
        },
        4: {
          area: ['全部', '内地', '港台', '日韩', '欧美', '其他'],
          year: ['全部', '2019', '2018', '2017', '2016', '2015', '2011-2014', '2000-2010', '90年代', '80年代', '更早']
        }
      },
      activeArr: {
        type: 0,
        area: 0,
        year: 0
      }
    }
  },
  methods: {
    getFilter () {
     
    },
    setFilter (index, index1) {
      this.activeArr[index] = index1
      this.$set(this.activeArr, index, index1)
      let data = {}
      this.type = this.type * 1
      if (this.type === 1 || this.type === 2) {
        data = {
          type: this.filterData[this.type].type[this.activeArr.type],
          area: this.filterData[this.type].area[this.activeArr.area],
          year: this.filterData[this.type].year[this.activeArr.year]
        }
      } else {
        data = {
          area: this.filterData[this.type].area[this.activeArr.area],
          year: this.filterData[this.type].year[this.activeArr.year]
        }
      }
      this.$emit('getFilter', data)
    }
  }
}
</script>
<style lang="scss" scoped>
.filter-list {
  padding-top: 15px;
}
.filter-type {
  white-space: nowrap;
  overflow-y: hidden;
  padding: 15px 30px;
  .filter-item-list {
    display: inline-block;
    padding: 0 30px;
    line-height: 54px;
    white-space: nowrap;
    font-size: 30px;
    &.active {
      background: #f4a82b;
      border-radius: 27px;
      color: #FFFFFF;
    }
    // min-width: 0;
  }
}
</style>



