hadow: 0 1px 0px 0 #f5f5f5;\n    .nav-list {\n      flex: 1;\n      min-width: 0;\n      display: flex;\n      padding: 0 30px;\n      justify-content: space-between;\n      flex-flow: row nowrap;\n      align-items: center;\n      color: #666;\n      .nav-item {\n        font-size: 32px;\n        line-height: 45px;\n        &.active{\n          font-size: 40px;\n          font-weight: 900;\n          line-height: 56px;\n          color: #CCA770;\n        }\n      }\n    }\n    .shareIcon {\n      background-image: url('../../../assets/img/share/black_share.png');\n      height: 88px;\n    }\n  }\n  // 搜索\n  .search-bar {\n    padding: 20px 30px 40px;\n    .search-content {\n      height: 66px;\n      display: flex;\n      align-items: center;\n      justify-content: center;\n      font-size: 26px;\n      line-height: 38px;\n      color: #C9A166;\n      opacity: .8;\n      background: #EEEEEE;\n      border-radius: 33px;\n      .search-icon {\n        width: 40px;\n        height: 40px;\n        margin-right: 30px;\n      }\n    }\n  }\n  // 轮播\n  .swiperContent {\n    display: flex;\n    flex-flow: row wrap;\n    align-items: center;\n    margin-top: 10px;\n    height: 680px;\n    overflow: hidden;\n  }\n  .rect {\n    height: 20px;\n    background: #f5f5f5;\n  }\n  // 下载引导\n  .download {\n    margin-bottom: 18px;\n    padding-top: 20px;\n    .downloadText {\n      height: 76px;\n      font-size: 26px;\n      line-height: 37px;\n      padding: 0 30px;\n      box-shadow: 0 1px 0 0 #F5F5F5;\n      background: #FDF5DE;\n      margin-bottom: 10px;\n      .message-icon {\n        width: 30px;\n        height: 30px;\n        margin-right: 10px;\n      }\n      .redColor {\n        color: #FF4112;\n      }\n      .text {\n        color: #666;\n        flex: 1;\n        text-align: left;\n      }\n      .enterEl {\n        color: #FF4112;\n        .arrowEl {\n          border-color: #FF4112;\n          width: 14px;\n          height: 14px;\n        }\n      }\n    }\n    .downimage {\n      width: 704px;\n      height: 194px;\n      margin: 0 auto;\n    }\n  }\n  // 有电影\n  .has_movie{\n    width: 685px;\n    height: 193px;\n    margin: 10px auto 29px;\n    border-radius: 20px;\n    overflow: hidden;\n    background: #FFFFFF;\n    box-shadow: -4px -4px 14px 0 #EBEAEF, 4px 4px 14px 0 #EBEAEF;\n    border-radius: 4px;\n    text-align: center;\n    .movie-title {\n      font-size: 32px;\n      line-height: 45px;\n      padding-top: 20px;\n      color: #F4A82B;\n    }\n    .movie-list {\n      display: flex;\n      flex-flow: row nowrap;\n      font-size: 28px;\n      color: #666;\n      margin-top: 20px;\n      line-height: 38px;\n      .movie-item {\n        flex: 1;\n        .movie-time {\n          margin-bottom: 10px;\n        }\n      }\n    }\n  }\n  // 观影用户\n  .watch_man {\n    width: 726px;\n    margin: 0 auto 50px;\n    color: #fff;\n    // background: #d3aa69;\n    // background: #e0d1b7;\n    background: #FFFFFF;\n    box-shadow: -4px -4px 14px 0 #EBEAEF, 4px 4px 14px 0 #EBEAEF;\n    border-radius: 4px;\n    overflow: hidden;\n    .watch-title {\n      text-align: center;\n      height: 60px;\n      font-size: 32px;\n      padding: 16px 0 12px;\n      box-sizing: border-box;\n      // background: #7f6741;\n      color: #333;\n      border-radius: 30px;\n      margin: 38px auto 0;\n    }\n    .watch-list {\n      display: flex;\n      flex-flow: row wrap;\n      padding: 36px 0 16px;\n      .watch-item {\n        width: 20%;\n        margin-bottom: 20px;\n        .avatar {\n          width: 80px;\n          height: 80px;\n          margin: 0 auto;\n          overflow: hidden;\n          border-radius: 50%;\n        }\n        img {\n          width: 100%;\n        }\n        .text {\n          font-size: 24px;\n          line-height: 40px;\n          text-align: center;\n          margin-top: 10px;\n        }\n      }\n    }\n    \n  }\n  // 列表\n  .index {\n    // margin-top: 30px;\n    .index-title {\n      display: flex;\n      padding: 22px 30px;\n      .more {\n        color: #666666;\n        font-size: 26px;\n        align-items: flex-end;\n        height: 30px;\n        line-height: 24px;\n        .arrowEl {\n          width: 12px;\n          height: 12px;\n          display: block;\n          border-color: #666;\n          margin-bottom: 6px;\n        }\n      }\n    }\n  }\n  .bottomTite {\n    background: #f5f5f5;\n    padding: 29px 30px 45px;\n    .b-title {\n      padding: 29px 0 19px;\n      font-weight: 900;\n      font-size: 28px;\n      color: #999;\n      line-height: 45px;\n      text-align: center;\n    }\n    .b-tip {\n      font-size: 24px;\n      line-height: 38px;\n      color: #999;\n      padding-bottom: 20px;\n      text-align: justify;\n    }\n  }\n  .mint-loadmore-top {\n    margin-top: -142px;\n    height: 140px;\n    padding-top: 30px;\n    box-sizing: border-box;\n    transition: all margin .4s;\n    &.loading {\n      margin-top: -100px;\n    }\n    .text{\n      white-space: nowrap;\n      text-align: center;\n      font-size: 22px;\n      color: #999;\n    }\n  }\n  .loading-logo {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    color: #666;\n    font-size: 28px;\n    margin: 0 auto 20px;\n    color: #ffcc00;\n    .logo {\n      width: 40px;\n      height: 40px;\n      margin-right: 10px;\n      animation: rotateFn 600ms linear infinite;\n    }\n    @keyframes rotateFn {\n      form {\n        transform: rotateZ(0deg);\n      }\n      to {\n        transform: rotateZ(360deg);\n      }\n    }\n  }\n  .rotateLoading {\n    height: 120px;\n    .download-icon{\n      width: 60px;\n      height: 60px;\n      overflow: hidden;\n      margin: 0 auto;\n      img {\n        width: 100%;\n      }\n    }\n    background-size: 100%;\n    margin: 0 auto;\n    // &.is-rotate {\n    //   transform: rotate(180deg)\n    // }\n  }\n}\n",null],"sourceRoot":""}]);

