<template>
  <div id='movieRecommend'>
    <mt-loadmore :top-method="loadTop" @translate-change="translateChange" :distance-index="1.2" @top-status-change="handleTopChange" :topDistance="70" ref="loadmore">
      <div slot="top" class="mint-loadmore-top" :class="{loading: topStatus==='loading'}">
        <!-- 向下 -->
        <div class="rotateLoading" v-show="topStatus !== 'loading'">
          <div class="download-icon download-gray">
            <img src="../../../assets/img/movie/download_gray.png" alt="加载">
          </div>
          <div class="text">{{topStatus === 'pull' ? '下拉推荐' : '松开刷新'}}</div>
        </div>
        <!-- 加载中 -->
        <div class="rotateLoading" v-show="topStatus === 'loading'">
          <div class="download-icon">
            <img src="../../../assets/img/movie/download.gif" alt="加载">
          </div>
          <div class="text">正在努力加载</div>
        </div>
      </div>
      <!-- 有数据展示 -->
      <div class="hadData" v-show="swiperList.length > 0" :class="{isBrowser: $config.source_style < 6}">
        <!-- 推荐轮播 -->
        <div class="swiperContent">
          <swiper class="swiperList" @toSearch="childSearch" :isRefresh='isRefresh' :list='swiperList'></swiper>
        </div>
        <div class="swipe" ref="swipe">
          <!-- 拥有的片库 -->
          <div class="has_movie">
            <div class="movie-title">掘金宝拥有史上最大最全影视片库</div>
            <div class="movie-list">
              <div class="movie-item">
                <div class="movie-time">覆盖时间</div>
                <div class="movie-time-value">1930-2019年</div>
              </div>
              <div class="movie-item">
                <div class="movie-time">收录资源</div>
                <div class="movie-time-value">{{details.movie_resources ? details.movie_resources.resources_count : 0 | setFloatNumber}}个</div>
              </div>
              <div class="movie-item">
                <div class="movie-time">今日更新</div>
                <div class="movie-time-value">{{details.movie_resources ? details.movie_resources.today_count : 0 | setFloatNumber}}个</div>
              </div>
            </div>
          </div>
          <!-- 下载引导 -->
          <div class="rect"></div>
          <div class="download" v-if="details.movie_show === 1">
            <div class="downloadText flex-row"  v-if="$config.source_style < 6" @click="toDownload">
              <div class="message-icon iconSize">
                <img src="../../../assets/img/my/message_active.png" alt="icon">
              </div>
              <div class="text">下载掘金宝app，<span class="redColor">永久免费</span>看最新最全资源！</div>
              <div class="flex-row enterEl">
                <div class="enter">进入</div>
                <div class="arrowEl"></div>
              </div>
            </div>
            <div class="downimage iconSize" @click="toVip">
              <!-- <img src="https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/webImage.png" alt="引导"> -->
              <img src="https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/webImage01.png" alt="引导">
            </div>
          </div>
          <!-- 院线大片 -->
          <div class="index" v-for="(item, index) in recommendList" :key="item.en">
            <div class="index-title jusB">
              <div class="index-title-text">{{item.name}}</div>
              <div class="more flex-row" @click="watchMore(index + 1)"><span>更多</span><div class="arrowEl"></div></div>
            </div>
            <div class="list">
              <list :list='item.list'  @toSearch="childSearch" :movieType='item.id'></list>
            </div>
          </div>
          <!-- 实时观影用户 -->
          <div class="watch_man" v-if="userList.count > 0">
            <div class="watch-title">实时观影用户{{userList.count}}位</div>
            <div class="watch-list" v-if="userList">
              <div class="watch-item" v-for="(item, index) in userList.user_list.slice(0, 10)" :key="index">
                <div class="avatar" v-if="item">
                  <img :src="item.avatar" alt="">
                </div>
                <!-- <div class="text">{{$utils.splitStr('花在风雨中')}}</div> -->
              </div>
            </div>
          </div>
          <!-- 提示 -->
          <div class="bottomTite">
            <div class="b-title">声明</div>
            <div class="b-tip">所有视频均收集引用于各大视频网站，本站只提供视频引用播放，不参与视频制作上传，不提供视频储存下载。</div>
            <div class="b-tip">若本站收录的节目无意侵犯了贵司版权，请将版权所属证明发至邮箱<a class="link" href="mailto://2813818384@qq.com">2813818384@qq.com</a>，我们会及时处理和回复，谢谢！</div>
          </div>
        </div>
        
      </div>
      <movie-loading v-show="swiperList.length === 0"></movie-loading>
      <!-- <movie-loading ></movie-loading> -->
    </mt-loadmore>
  </div>
</template>
<script>
import swiper from '../swiper'
import {mapState, mapMutations} from 'vuex'
import List from '@/components/movie/common/list'
import Search from '@/components/movie/common/showSearch'
import MovieLoading from '@/components/movie/common/video-loading'
export default {
  components: {
    swiper,
    List,
    Search,
    MovieLoading
  },
  computed: {
    ...mapState({
      commonState: state => state.common,
      movie: state => state.movie
    })
  },
  props: {
    movieList: {
      type: Array
    }
  },
  data () {
    return {
      topStatus: 'loading',
      showSearch: false,
      movieType: 0,
      kw: '',
      userList: {},
      movieKey: {
        电影: 1,
        电视剧: 2,
        综艺: 3,
        动漫: 4
      },
      recommendList: [],
      swiperList: [],
      navIndex: 0,                        // 推荐分类激活
      activeIndex: 0,
      details: {},
      watchList: {},
      movieScroll: {
        0: 0,
        1: 0,
        2: 0,
        3: 0,
        4: 0
      },
      navList: [{
        name: '推荐',
        id: 0
      },
      {
        name: '电影',
        id: 1
      },
      {
        name: '电视剧',
        id: 2
      },
      {
        name: '综艺',
        id: 3
      },
      {
        name: '动漫',
        id: 4
      }],
      isRefresh: 0
    }
  },
  methods: {
    ...mapMutations(['movie_setShowSearch']),
    childSearch ({item, movieType}) {
      this.$emit('childSearch', {item, movieType})
    },
    // 关闭函数
    closeFn () {
      this.movie_setShowSearch(false)
    },
    // 下拉刷新状态
    loadTop () {
      this.getRecommend(res => {
        setTimeout(() => {
          this.$refs.loadmore.onTopLoaded()
          this.isRefresh++
        }, 1000)
      })
      this.getUser()
      // this.getSwiperList()
    },
    // 监听移动距离
    translateChange (translate) {
      const translateNum = +translate
      this.translate = translateNum.toFixed(2)
      this.moveTranslate = (1 + translateNum / 140).toFixed(2)
    },
    // 监听移动状态听状态
    handleTopChange (status) {
      this.moveTranslate = 2
      this.topStatus = status
    },
    // 获取轮播数据
    getSwiperList (cb) {
      let data = {
        type: '电影',
        limit: 8
      }
      this.$api.get('MOVIE_SWIPER', data, res => {
        // for (let v of res.data) {
        //   v.actor = v.actor.repeat(4)
        // }
        this.swiperList = res.data
        cb && cb(res)
      })
    },
    watchMore (index) {
      this.$emit('watchMore', index)
    },
    // 跳转至搜索
    toSearch () {
      this.$router.push('/movie/search')
    },
    randomList (arr, length) {
      let a = parseInt(Math.random() * arr.length)
      return arr.slice(a, a + length)
    },
    getRecommend (cb, flag) {
      this.$api.get('MOVIE_INDEX_RECOMMEND', {}, res => {
        if (flag) {
          this.swiperList = [...this.randomList(res.data.tv, 3), ...this.randomList(res.data.variety, 3), ...this.randomList(res.data.cartoon, 3)]
        }
        this.recommendList = this.movieList.map(item => {
          let tempObj = item
          tempObj.list = res.data[item.en]
          tempObj.id = this.movieKey[item.category1]
          return tempObj
        })
        this.details = res.data
        cb && cb(res)
      })
    },
    // 获取实时在线用户
    getUser () {
      this.$api.get('ONLINE_USER', {}, res => {
        // res.data.user_list = [...res.data.user_list, ...res.data.user_list]
        this.userList = res.data
      })
    },
    // 去下载电影
    toDownload () {
      this.$utils.openDownUrl()
    },
    // 跳转至解析地址
    toVip () {
      this.$router.push('/movie_decode')
    },
    // 初始化数据
    initData () {
      this.getRecommend(res => {
        this.initSwipe()
      }, 1)
      this.getUser()
    },
    // 初始化滑动事件
    initSwipe () {
      this.$refs.swipe.addEventListener('swipeleft', this.swipeLeft)
      this.$refs.swipe.addEventListener('swiperight', this.swipeRight)
    },
    swipeLeft () {
      this.$emit('swipe', {actions: 'swipeleft'})
    },
    swipeRight () {
      this.$emit('swipe', {actions: 'swiperight'})
    }
  },
  created () {
    this.filterType = this.navList[this.activeIndex].name
    if (this.activeIndex === 2) {
      this.listType = 0
    } else {
      this.listType = 5
    }
    this.initData()
  },
  mounted () {
    this.initSwipe()
  }
}
</script>

<style lang="scss" scoped>
.hadData {
  &.isBrowser {
    padding-bottom: 130px;
  }
}
#movieRecommend {
  padding-bottom: 100px;
  height: 100%;
  overflow: auto;
  .header-nav {
    display: flex;
    flex-flow: row nowrap;
    padding-right: 100px;
    height: 88px;
    box-shadow: 0 1px 0px 0 #f5f5f5;
    .nav-list {
      flex: 1;
      min-width: 0;
      display: flex;
      padding: 0 30px;
      justify-content: space-between;
      flex-flow: row nowrap;
      align-items: center;
      color: #666;
      .nav-item {
        font-size: 32px;
        line-height: 45px;
        &.active{
          font-size: 40px;
          font-weight: 900;
          line-height: 56px;
          color: #CCA770;
        }
      }
    }
    .shareIcon {
      background-image: url('../../../assets/img/share/black_share.png');
      height: 88px;
    }
  }
  // 搜索
  .search-bar {
    padding: 20px 30px 40px;
    .search-content {
      height: 66px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 26px;
      line-height: 38px;
      color: #C9A166;
      opacity: .8;
      background: #EEEEEE;
      border-radius: 33px;
      .search-icon {
        width: 40px;
        height: 40px;
        margin-right: 30px;
      }
    }
  }
  // 轮播
  .swiperContent {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    margin-top: 10px;
    height: 680px;
    overflow: hidden;
  }
  .rect {
    height: 20px;
    background: #f5f5f5;
  }
  // 下载引导
  .download {
    margin-bottom: 18px;
    padding-top: 20px;
    .downloadText {
      height: 76px;
      font-size: 26px;
      line-height: 37px;
      padding: 0 30px;
      box-shadow: 0 1px 0 0 #F5F5F5;
      background: #FDF5DE;
      margin-bottom: 10px;
      .message-icon {
        width: 30px;
        height: 30px;
        margin-right: 10px;
      }
      .redColor {
        color: #FF4112;
      }
      .text {
        color: #666;
        flex: 1;
        text-align: left;
      }
      .enterEl {
        color: #FF4112;
        .arrowEl {
          border-color: #FF4112;
          width: 14px;
          height: 14px;
        }
      }
    }
    .downimage {
      width: 704px;
      height: 194px;
      margin: 0 auto;
    }
  }
  // 有电影
  .has_movie{
    width: 685px;
    height: 193px;
    margin: 10px auto 29px;
    border-radius: 20px;
    overflow: hidden;
    background: #FFFFFF;
    box-shadow: -4px -4px 14px 0 #EBEAEF, 4px 4px 14px 0 #EBEAEF;
    border-radius: 4px;
    text-align: center;
    .movie-title {
      font-size: 32px;
      line-height: 45px;
      padding-top: 20px;
      color: #F4A82B;
    }
    .movie-list {
      display: flex;
      flex-flow: row nowrap;
      font-size: 28px;
      color: #666;
      margin-top: 20px;
      line-height: 38px;
      .movie-item {
        flex: 1;
        .movie-time {
          margin-bottom: 10px;
        }
      }
    }
  }
  // 观影用户
  .watch_man {
    width: 726px;
    margin: 0 auto 50px;
    color: #fff;
    // background: #d3aa69;
    // background: #e0d1b7;
    background: #FFFFFF;
    box-shadow: -4px -4px 14px 0 #EBEAEF, 4px 4px 14px 0 #EBEAEF;
    border-radius: 4px;
    overflow: hidden;
    .watch-title {
      text-align: center;
      height: 60px;
      font-size: 32px;
      padding: 16px 0 12px;
      box-sizing: border-box;
      // background: #7f6741;
      color: #333;
      border-radius: 30px;
      margin: 38px auto 0;
    }
    .watch-list {
      display: flex;
      flex-flow: row wrap;
      padding: 36px 0 16px;
      .watch-item {
        width: 20%;
        margin-bottom: 20px;
        .avatar {
          width: 80px;
          height: 80px;
          margin: 0 auto;
          overflow: hidden;
          border-radius: 50%;
        }
        img {
          width: 100%;
        }
        .text {
          font-size: 24px;
          line-height: 40px;
          text-align: center;
          margin-top: 10px;
        }
      }
    }
    
  }
  // 列表
  .index {
    // margin-top: 30px;
    .index-title {
      display: flex;
      padding: 22px 30px;
      .more {
        color: #666666;
        font-size: 26px;
        align-items: flex-end;
        height: 30px;
        line-height: 24px;
        .arrowEl {
          width: 12px;
          height: 12px;
          display: block;
          border-color: #666;
          margin-bottom: 6px;
        }
      }
    }
  }
  .bottomTite {
    background: #f5f5f5;
    padding: 29px 30px 45px;
    .b-title {
      padding: 29px 0 19px;
      font-weight: 900;
      font-size: 28px;
      color: #999;
      line-height: 45px;
      text-align: center;
    }
    .b-tip {
      font-size: 24px;
      line-height: 38px;
      color: #999;
      padding-bottom: 20px;
      text-align: justify;
    }
  }
  .mint-loadmore-top {
    margin-top: -142px;
    height: 140px;
    padding-top: 30px;
    box-sizing: border-box;
    transition: all margin .4s;
    &.loading {
      margin-top: -100px;
    }
    .text{
      white-space: nowrap;
      text-align: center;
      font-size: 22px;
      color: #999;
    }
  }
  .loading-logo {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #666;
    font-size: 28px;
    margin: 0 auto 20px;
    color: #ffcc00;
    .logo {
      width: 40px;
      height: 40px;
      margin-right: 10px;
      animation: rotateFn 600ms linear infinite;
    }
    @keyframes rotateFn {
      form {
        transform: rotateZ(0deg);
      }
      to {
        transform: rotateZ(360deg);
      }
    }
  }
  .rotateLoading {
    height: 120px;
    .download-icon{
      width: 60px;
      height: 60px;
      overflow: hidden;
      margin: 0 auto;
      img {
        width: 100%;
      }
    }
    background-size: 100%;
    margin: 0 auto;
    // &.is-rotate {
    //   transform: rotate(180deg)
    // }
  }
}
</style>



