<template>
  <div id="showSearch" style="z-index: 5" @click="closeFn">
    <div class="showSearchContent" @click.stop="''" infinite-scroll-distance="200">
      <div class="title">
        <div class="text"><span class="magic">{{$utils.splitStr(kw, 12)}}</span>的相关资源：</div>
        <div class="close"  @click="closeFn"></div>
      </div>
      <div class="tabs border-1px" v-show="list.length > 0 || tabs.length > 0">
        <div class="tabs-item" @click="setActive(index)" v-for="(item, index) in tabs" :key="index" v-show="item.count !== 0" :class="{active: activeIndex === index}">{{item.name}}{{item.count}}</div>
      </div>
      <div class="scrollTouch" ref="showSearch">
        <div v-infinite-scroll="loadMore" infinite-scroll-distance="200">
          <list :list="list" :type="4"></list>
          <loading v-if="loading === 1 && page === 1"></loading>
          <list-loading v-if="loading === 1 && list.length > 0" :loading="loading"></list-loading>
          <div class="complete" v-if="loading === 2 && list.length > 0">没有更多数据！</div>
          <div class="noList" v-if="list.length === 0 && loading === 2">
            <div class="iconSize">
              <img src="../../../assets/img/movie/no_movie.png" alt="暂无影视">
            </div>
            <div class="text">暂无相关资源~</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import List from '@/components/movie/common/list'
import ListLoading from '@/components/movie/common/list-loading'
import loading from '@/components/movie/common/video-loading'
export default {
  components: {
    List,
    ListLoading,
    loading
  },
  data () {
    return {
      activeIndex: 0,
      tabs: [],
      types: {
        1: '电影',
        2: '电视剧',
        3: '综艺',
        4: '动漫'
      },
      loading: 1,
      page: 1,
      total: 0,
      list: []
    }
  },
  computed: {
    movie () {
      return this.$store.state.movie
    }
  },
  methods: {
    // 关闭浮层
    closeFn () {
      this.$emit('closeFn')
    },
    // 获取搜索结果
    getResult (cb) {
      if (!this.kw) {
        return
      }
      let data = {
        page: this.page,
        kw: this.kw
      }
      if (this.activeIndex) {
        data.type = this.tabs[this.activeIndex].name
      }
      this.$api.get('MOVIE_SEARCH', data, res => {
        this.total = res.data.total
        let reg = new RegExp(`${this.kw}`, 'g')
        if (data.page === 1) {
          res.data.count.unshift({
            name: '全部'
          })
          this.tabs = res.data.count
        }
        for (let v of res.data.data) {
          v.vod_name = v.vod_name.replace(reg, `<span class="magic">${this.kw}</span>`)
          if (v.vod_remarks.indexOf('HD') > -1) {
            v.colorType = 1
          } else if (v.vod_remarks.indexOf('超清') > -1) {
            v.colorType = 2
          } else if (v.vod_remarks.indexOf('高清') > -1) {
            v.colorType = 3
          } else if (v.vod_remarks.indexOf('DVD') > -1) {
            v.colorType = 4
          } else if (v.vod_remarks.indexOf('正片') > -1) {
            v.colorType = 5
          } else if (v.vod_remarks.indexOf('BD') > -1) {
            v.colorType = 6
          }
        }
        cb && cb(res.data.data)
        this.loading = 0
        if (this.page >= res.data.last_page) {
          this.loading = 2
        }
        this.page++
      })
    },
    setActive (index) {
      if (index === this.activeIndex) {
        return
      } else {
        this.activeIndex = index
      }
      this.list = []
      this.loading = 1
      this.page = 1
      this.getResult(res => {
        this.list = res
      })
    },
    loadMore () {
      if (this.loading > 0) {
        return
      }
      this.loading = 1
      this.getResult(res => {
        this.list = [...this.list, ...res]
      })
    }
  },
  props: {
    kw: {
      type: String,
      default: '你好'
    },
    activeType: {
      type: Number,
      default: 1
    }
  },
  watch: {
    'movie.movieBack.count' (flag) {
      this.$utils.cache('showSearchScroll', this.$refs.showSearch.scrollTop)
    },
    'movie.movieActive' () {
      if (this.$utils.cache('showSearchScroll')) {
        this.$refs.showSearch.scrollTop = this.$utils.cache('showSearchScroll')
      }
    }
  },
  mounted () {
    this.getResult(res => {
      this.list = res
    })
  }
}
</script>

<style lang="scss" scoped>
#showSearch{
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  height: 100vh;
  background: rgba($color: #000000, $alpha: .5);
  .tabs {
    height: 100px;
    padding: 0 20px;
    overflow-x: auto;
    overflow-y: hidden;
    -webkit-overflow-scrolling: touch;
    white-space: nowrap;
    .tabs-item {
      display: inline-block;
      margin-top: 20px;
      height: 60px;
      padding: 10px 30px;
      box-sizing: border-box;
      line-height: 44px;
      border: 1px solid #f4a828;
      border-radius: 30px;
      align-items: center;
      margin-right: 30px;
      &.active {
        background: #f4a828;
        color: #fff;
      }
    }
  }
  .noList {
    padding: 120px 0;
    text-align: center;
    .iconSize {
      margin: 0 auto 30px;
      width: 130px;
      height: 130px;
    }
    .text {
      font-size: 32px;
      color: #666666;
      line-height: 45px;
    }
  }
  .showSearchContent {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 967px;
    background: #fff;
    border-radius: 35px 35px 0 0;
    overflow: hidden;
    .title {
      height: 106px;
      border-radius: 30px 30px 0 0;
      padding: 0 30px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1px solid #eee;
      .text {
        font-weight: 900;
        font-size: 32px;
      }
      .close {
        width: 60px;
        height: 60px;
        background: url('../../../assets/img/login_close.png') no-repeat center;
        background-size: 27px 27px;
        margin-right: -14px;
      }
    }
    .scrollTouch {
      height: calc(100% - 206px);
      overflow: auto;
      padding-bottom: 40px;
      box-sizing: border-box;
      &.hasTab{
        margin-top: 100px;
      }
    }
  }
  .complete {
    padding: 30px 0;
    line-height: 45px;
    font-size: 30px;
    text-align: center;
  }
}
</style>



